<style lang="less" >
    .clearfix{
        padding-top: 20px;
        display: flex;
        justify-content: center;
        .avatar-uploader > .ant-upload {
          width: 200px;
          height: 200px;
        }

        .ant-upload-select-picture-card i {
          font-size: 32px;
          color: #999;
        }

        .ant-upload-select-picture-card .ant-upload-text {
          margin-top: 8px;
          color: #666;
        }
    }
    .imgTxt{
      display: flex;
      justify-content: center;
      align-items: center;
      .u{
        font-size: 16px;
      }
      .f{
        margin-left: 10px;
      }
    }
    .maxImg{
      max-width: 400px;
    }
</style>


<template>
  <div class="clearfix">
    <div>
      <a-upload
        :customRequest="uploadFile"
        list-type="picture-card"
        class="avatar-uploader"
        :show-upload-list="false"
        multiple
      >
      <img class="maxImg" v-if="imageUrl" :src="imageUrl + `?v=${Math.floor(Math.random() * 1000000000000)}`" alt="avatar" />
      <div v-else>
        <loading-outlined v-if="loading"></loading-outlined>
        <plus-outlined v-else></plus-outlined>
        <div class="ant-upload-text">Upload</div>
      </div>
      </a-upload>
    </div>

  </div>

  <div class="imgTxt" v-if="imageUrl">
    <div class="u">{{imageUrl}}</div>
    <div class="f" @click="copy"><a-button type="primary">点击复制链接</a-button></div>
  </div>
 

  
</template>
<script>
import { PlusOutlined } from '@ant-design/icons-vue';
import { defineComponent, ref, toRef, toRefs, watch, nextTick, onUnmounted } from 'vue';
import axios from 'axios';
import ClipboardJS from  "clipboard"
import { message } from 'ant-design-vue';
import {postRequest} from '@/utils/request';
import { useStore, mapState } from 'vuex'


function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

export default defineComponent({
  props:{
     show: {
        type : Boolean,
        default: true
     }
  },
  components: {
    PlusOutlined
  },
  setup(props, context) {
    const imageUrl = ref('')
    let clip  = null
    const store = useStore()

    onUnmounted( () => {
          clip && clip.destroy();
    })
    nextTick( ()=> {
      clip  = new ClipboardJS('.imgTxt',{
          text: function(trigger) {
            message.success(`复制了 ${trigger.childNodes[0].innerText} ！`);
            return trigger.childNodes[0].innerText
          }
      })
    })
    
    /* 关闭modal时候清空图片记录 */
    watch(props, (newValue, oldValue) => {
        if(!newValue.show){
          imageUrl.value=""
        }
    })
    
    //文件上传
    const uploadFile = async(file) => {
        let forms = new FormData()
        let configs = {
            headers:{'Content-Type':'multipart/form-data'},
        };
        forms.append('file',file.file)
         try{
           let _interface = store.state.ossType === '七牛云'? '/File/uploadFile' : '/File/uploadAliYun'
           let res = await postRequest( _interface, forms, configs)
         
            imageUrl.value= res.url 
            const {key, hash}= res
            context.emit('infoclick',{
                url: imageUrl.value,
                picName: file.file.name,
                key,
                hash
            })
         }catch(err){
           console.log(err, 'err')
         }
    }

    return {
      imageUrl,
      uploadFile
    };
  },

 
});
</script>
<style>
/* you can make up upload button and sample style by using stylesheets */
.ant-upload-select-picture-card i {
  font-size: 32px;
  color: #999;
}

.ant-upload-select-picture-card .ant-upload-text {
  margin-top: 8px;
  color: #666;
}
</style>